<template lang="html">


    <div class="usluga">
      <div class="headUsluga">


  <router-link class="navbar-brand" to="/ksusluge">Back </router-link>
      <h3>Performance Management</h3>
      <p>Performance Management je alat kojim se ostvaruju Kompanijski KPI-evi, meri efikasnost učinka svakog zaposlenog  i pomoću kog se kreira sistem nagrade kvalitetnih zaposlenih u Kompaniji.</p>
      <h5>Potrebna Vam je ova usluga? Popunite polja ispod:</h5>
            </div>
      <div class="card-body">


           <div class="form-group">
             <label for="numberOfEmployees">Broj zaposlenih za koje se vrši usluga</label>
             <input
               class="form-control"
               type="text"
               placeholder=""
               name="numberOfEmployees"
               v-model="numberOfEmployees"
               id="numberOfEmployees"
               >
           </div>
          <div class="form-group">
            <label for="companyName">Naziv Kompanije</label>
            <input
              class="form-control"
              type="text"
              placeholder=""
              name="companyName"
              v-model="companyName"
              id="companyName"
              >
          </div>
          <div class="form-group">
            <label for="contactName">Ime i Prezime Kontakt Osobe</label>
            <input
              class="form-control"
              type="text"
              placeholder=""
              name="contactName"
              v-model="contactName"
              id="contactName"
              >
          </div>
          <div class="form-group">
            <label for="contactNumber">Kontakt telefon</label>
            <input
              class="form-control"
              type="text"
              placeholder=""
              name="contactNumber"
              v-model="contactNumber"
              id="contactNumber"
              >
          </div>
          <div class="form-group">
            <label for="email">E-mail</label>
            <input
              class="form-control"
              type="text"
              placeholder=""
              name="email"
              v-model="email"
              id="email"
              >
          </div>




          <button class="btn btn-primary" @Click="submit">Posalji Formular</button>
          &nbsp;&nbsp;&nbsp;&nbsp;



    </div>
  </div>



</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {

  name: 'PerformanceMenagement',
  components: {

  },
  data(){
    return{
      companyName:"",
  contactName:"",
  contactNumber:"",
  email:"",
  numberOfEmployees :"",
    }
  },
  methods:{
  ...mapActions(['submitServiceAplication']),
  submit(){



    let text = [];
    text.push("Performance Management");
    text.push("Naziv Kompanije: "+this.companyName);
    text.push("Ime i Prezime Kontakt Osobe: "+this.contactName);
    text.push("Email: "+this.email);
    text.push("Kontakt telefon: "+this.contactNumber  );
    text.push("Broj zaposlenih za koje se vrši usluga: "+this.numberOfEmployees);

    let data ={
      formular:text
    }

    this.submitServiceAplication(data);
  },
},

  beforeCreate: function() {
        document.body.className = 'usluge';
  }
}
</script>

<style lang="css" scoped>
.usluga{
  position: absolute;
  top: auto;
  left: 10%;
  right: 10%;
  background-color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  margin-top: 15px;
}
.headUsluga{
  background-color: #77bbee;
  padding: 10px;
  border-top-left-radius:  10px;
  border-top-right-radius:  10px;
}
.card-body{
  background-color: #bbffff;
  border-bottom-left-radius:  10px;
  border-bottom-right-radius:  10px;
}
